import { dictionaryDataPipeline } from './pipeline';
import { DictionaryDataPipelineOptions } from '../interfaces';

export { dictionaryDataPipeline } from './pipeline';

export const getDictionaryData = async (context: DictionaryDataPipelineOptions) => {
    return dictionaryDataPipeline
        .run({
            ...context,
            dictionary: null,
        })
        .then((context) => context.dictionary);
};
